import BibleItem from '../components/BibleItem.vue'
import bibleDate from '../db/bible.js'

export default {
  components: {
    BibleItem
  },
  data() {
    return {
      type: this.$route.params.type,
      values: bibleDate.filter(this.filterConditions)
    }
  },
  methods: {
    filterConditions: function(element, index, array) {
      let type = this.$route.params.type;
      return element.theme.indexOf(type) > -1;
    }
  },
  watch: {
    '$route' (to, from) {
      this.type = to.params.type;
      this.values = bibleDate.filter(this.filterConditions);
    }
  }
}



